<template>
  <transition name="modal">
    <div class="modal-mask">
      <div class="flexbox align-items-center w-100">
        <div class="modal-container mr-auto p-1 bg-white">
          <div class="default-layout layout_ flexbox flex-dir-col" v-if="modalLayout == 'default'">
            <div class="header flexbox justify-between align-items-center p-1 mobile-p-sm">
              <button class="btn btn-plain w-auto invisible">
                <i class="fa fa-arrow-left"></i>
              </button>
              <h4 class="title font-h-2 mobile-font-sm">Join Modern Account</h4>
              <button class="btn btn-plain w-auto" @click="$emit('close')">
                <img src="@/assets/icons/close-icon.svg" alt="Close Icon" style="width: 25px;" />
              </button>
            </div>
            <div class="body flex-1 flex-dir-col flexbox justify-center align-items-center p-2 mobile-p-1">
              <button
                class="btn btn-outline btn-inline mr-b-1"
                @click="modalLayout = 'signupform'"
                disabled
              >Sign up with email</button>
              <button
                class="btn btn-outline btn-inline mr-b-1"
                @click="modalLayout = 'loginform'"
                disabled
              >Sign in</button>
              <div class="separator">OR</div>
              <button class="text-btn" @click="$emit('demoModalContinue')">Continue as Guest</button>
            </div>
          </div>

          <form
            class="login-form layout_"
            v-if="modalLayout == 'loginform'"
            action="#"
            method="post"
          >
            <div class="header flexbox justify-between align-items-center p-1 mobile-p-sm">
              <button class="btn btn-plain w-auto" @click="modalLayout = 'default'">
                <img
                  src="@/assets/icons/arrow-left-icon.svg"
                  alt="Left Arrow Icon"
                  style="width: 40px;"
                />
              </button>
              <h4 class="title font-h-2 mobile-font-sm">Sign In</h4>
              <button class="btn btn-plain w-auto" @click="$emit('close')">
                <img src="@/assets/icons/close-icon.svg" alt="Close Icon" style="width: 25px;" />
              </button>
            </div>
            <div class="body flex-1 flex-dir-col flexbox justify-center align-items-center p-2 mobile-p-1">
              <div class="block">
                <div class="form-row">
                  <div class="form-group w-100 p-0 mr-b-1">
                    <label for="login_phone" class="sr-only">Phone Number</label>
                    <div class="input-pair">
                      <select class="form-control country-code selector">
                        <option value="254">+254</option>
                        <option value="255">+255</option>
                        <option value="253">+253</option>
                      </select>
                      <input
                        type="tel"
                        id="login_phone"
                        placeholder="Phone Number"
                        required
                        class="form-control"
                      />
                    </div>
                  </div>
                </div>
                <div class="form-group p-0 mr-b-1">
                  <label for="login_password" class="sr-only">Password</label>
                  <input
                    type="password"
                    id="login_password"
                    placeholder="Password"
                    required
                    class="form-control"
                  />
                </div>
                <div class="form-group mr-b-1">
                  <button
                    type="button"
                    class="btn w-100"
                    @click="$emit('demoModalContinue')"
                  >SIGN IN</button>
                </div>
                <div class="flexbox justify-between align-items-center">
                  <a
                    href="#"
                    @click.prevent="modalLayout = 'resetPasswordform'"
                    class="display-inline-block"
                  >Forgot Password</a>
                  <a
                    href="#"
                    @click.prevent="modalLayout = 'signupform'"
                    class="display-inline-block"
                  >Sign Up</a>
                </div>
              </div>
            </div>
          </form>

          <form
            class="signup-form layout_"
            v-if="modalLayout == 'signupform'"
            action="#"
            method="post"
          >
            <div class="header flexbox justify-between align-items-center p-1 mobile-p-sm">
              <button class="btn btn-plain w-auto" @click="modalLayout = 'default'">
                <img
                  src="@/assets/icons/arrow-left-icon.svg"
                  alt="Left Arrow Icon"
                  style="width: 40px;"
                />
              </button>
              <h4 class="title font-h-2 mobile-font-sm">Sign Up</h4>
              <button class="btn btn-plain w-auto" @click="$emit('close')">
                <img src="@/assets/icons/close-icon.svg" alt="Close Icon" style="width: 25px;" />
              </button>
            </div>
            <div class="body flex-1 flex-dir-col flexbox justify-center align-items-center p-2 mobile-p-1">
              <div class="inline">
                <div class="form-row flexbox justify-between align-items-center">
                  <div class="form-group p-0 mr-b-1 w-50-gap mobile-w-100">
                    <label for="signup_name" class="sr-only">Name</label>
                    <input
                      type="text"
                      id="signup_name"
                      placeholder="Name"
                      required
                      class="form-control"
                    />
                  </div>
                  <div class="form-group p-0 mr-b-1 w-50-gap mobile-w-100">
                    <label for="signup_email" class="sr-only">Email</label>
                    <input
                      type="email"
                      id="signup_email"
                      placeholder="Email"
                      required
                      class="form-control"
                    />
                  </div>
                </div>
                <div class="form-row flexbox justify-between align-items-center">
                  <div class="form-group p-0 mr-b-1 w-50-gap mobile-w-100">
                    <label for="signup_dob" class="sr-only">Gender</label>
                    <select class="form-control">
                      <option value>Select Gender</option>
                      <option value="Male">Male</option>
                      <option value="Female">Female</option>
                    </select>
                  </div>
                  <div class="form-group p-0 mr-b-1 w-50-gap mobile-w-100">
                    <label for="signup_dob" class="sr-only">Date of Birth</label>
                    <input
                      type="date"
                      id="signup_dob"
                      placeholder="Date of Birth"
                      required
                      class="form-control"
                    />
                  </div>
                </div>
                <div class="form-row flexbox justify-between align-items-center">
                  <div class="form-group p-0 mr-b-1 w-50-gap mobile-w-100">
                    <label for="signup_phone" class="sr-only">Phone Number</label>
                    <div class="input-pair">
                      <select class="form-control country-code selector">
                        <option value="254">+254</option>
                        <option value="255">+255</option>
                        <option value="253">+253</option>
                      </select>
                      <input
                        type="tel"
                        id="signup_phone"
                        placeholder="Phone Number"
                        required
                        class="form-control"
                      />
                    </div>
                  </div>
                  <div class="form-group p-0 mr-b-1 w-50-gap mobile-w-100">
                    <label for="signup_password" class="sr-only">Password</label>
                    <input
                      type="password"
                      id="signup_password"
                      placeholder="Password"
                      required
                      class="form-control"
                    />
                  </div>
                </div>
                <div class="form-group p-0 mr-b-1 text-center p-t-1 p-b-snm">
                  <button
                    type="button"
                    class="btn btn-inline mr-auto"
                    @click="$emit('demoModalContinue')"
                  >SIGN UP</button>
                </div>
                <div class="p-sm flexbox justify-center align-items-center text-center">
                  <span class="display-inline-block mr-l-sm">Already have an account</span>
                  <a
                    href="#"
                    @click.prevent="modalLayout = 'loginform'"
                    class="display-inline-block mr-l-sm"
                  >Sign In</a>
                </div>
              </div>
            </div>
          </form>

          <form
            class="resetpassword-form layout_"
            v-if="modalLayout == 'resetPasswordform'"
            action="#"
            method="post"
          >
            <div class="header flexbox justify-between align-items-center p-1 mobile-p-sm">
              <button class="btn btn-plain w-auto" @click="modalLayout = 'default'">
                <img
                  src="@/assets/icons/arrow-left-icon.svg"
                  alt="Left Arrow Icon"
                  style="width: 40px;"
                />
              </button>
              <h4 class="title font-h-2 mobile-font-sm">Reset Password</h4>
              <button class="btn btn-plain w-auto" @click="$emit('close')">
                <img src="@/assets/icons/close-icon.svg" alt="Close Icon" style="width: 25px;" />
              </button>
            </div>
            <div class="body flex-1 flex-dir-col flexbox justify-center align-items-center p-2 mobile-p-1">
              <div class="block">
                <div class="form-row flexbox justify-between align-items-center">
                  <div class="form-group w-100">
                    <label for="reset_phone" class="sr-only">Phone Number</label>
                    <div class="input-pair">
                      <select class="form-control country-code selector">
                        <option value="254">+254</option>
                        <option value="255">+255</option>
                        <option value="253">+253</option>
                      </select>
                      <input
                        type="tel"
                        id="reset_phone"
                        placeholder="Phone Number"
                        required
                        class="form-control"
                      />
                    </div>
                  </div>
                </div>
                <div class="form-group p-0 mr-b-1 mr-t-1">
                  <button
                    type="button"
                    class="btn w-100"
                    @click="$emit('demoModalContinue')"
                  >SEND CODE</button>
                </div>
                <div class="flexbox justify-center align-items-center">
                  <a
                    href="#"
                    @click.prevent="modalLayout = 'loginform'"
                    class="display-inline-block"
                  >Sign In Here</a>
                </div>
              </div>
            </div>
          </form>

          <!-- <button class="modal-default-button" @click="$emit('close')">OK</button> -->
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
export default {
  name: "AppModal",
  data() {
    return {
      modalLayout: "default",
      travelDate: null
    };
  },
  methods: {
    loginRequest: function(event) {
      event.preventDefault();
    }
  },
  mounted() {
    console.log("Loader");
  }
};
</script>
