<template>
  <div class="page-wrapper buses-page">
    <!-- App Header -->
    <AppHeader layoutType="layout-2"></AppHeader>

    <section class="viewport-h-min-100">
      <!-- Include AppLoader (Show Progress On Page) -->
      <AppLoader v-if="show_Loader" />

      <div class="seat-labels">
        <div class="container">
          <div class="flexbox align-items-center">
            <img src="@/assets/icons/seat-business.svg" alt />
            <span>Business</span>
          </div>
          <div class="flexbox align-items-center">
            <img src="@/assets/icons/seat-first-class.svg" alt />
            <span>First Class</span>
          </div>
          <div class="flexbox align-items-center">
            <img src="@/assets/icons/seat-vip.svg" alt />
            <span>VIP</span>
          </div>
          <div class="flexbox align-items-center">
            <img src="@/assets/icons/seat-selected.svg" alt />
            <span>Selected</span>
          </div>
          <div class="flexbox align-items-center">
            <img src="@/assets/icons/booked.svg" alt />
            <span>Booked</span>
          </div>
        </div>
      </div>

      <div class="container p-1 pos-relative">
        <!-- Loader -->
        <div
          class="bus-progress-loader flexbox align-items-center justify-center zindex-10"
          v-if="!busSeatLoadingComplete"
        >
          <div class="text-primary">
            <i class="fas fa-spinner fa-pulse fa-5x"></i>
          </div>
        </div>

        <!-- Loader -->
        <div
          class="loader bus-progress-loader flexbox align-items-center justify-center zindex-10"
          v-show="show_Loader"
        >
          <div class="text-primary">
            <i class="fas fa-spinner fa-pulse fa-5x"></i>
          </div>
        </div>
        <!--Loader-->

        <div
          class="title-wrapper with-icon pos-relative flexbox justify-center align-items-center p-t-xs p-b-xs mr-t-2 mr-b-2 mobile-mr-t-1 mobile-mr-b-1"
        >
          <span class="p-l-2 p-r-2 mobile-p-l-0 mobile-p-r-0 align-items-center zindex-5">
            <img src="@/assets/icons/bus-icon-blue.svg" alt="Book Bus" />
            <h3 class="title font-h-2 mobile-font-sm">Select your seats</h3>
          </span>
        </div>

        <div class="row seat-layout-row p-t-1 pos-relative">
          <div class="bus-seat-wrapper for-desktop p-b-2">
            <table border="0" cellpadding="2" cellspacing="1" class="bus-body w-100">
              <tr v-for="(row_, index) in  activeTableCols" :key="index">
                <td v-for="(col_, index) in activeTableRows" :key="index">
                  <div v-for="(seat_, index) in seats" :key="index">
                    <div
                      :class="{'seat-item':true, 'business':seat_.seat_type === 'Business', 'vip':seat_.seat_type === 'VIP', 'first-class':seat_.seat_type === 'First Class'}"
                      v-if="row_ == seat_.seat_col && col_ == seat_.seat_row"
                    >
                      <input
                        type="checkbox"
                        :id="`seat-${seat_.seat_col}-${seat_.seat_row}`"
                        class="seat-checkbox"
                        @change="seatSelected(seat_, $event)"
                        v-bind:disabled="seat_.seat_number == 'Dr' || seat_.seat_status == 'reserved'"
                        :checked="isSeatSelected(seat_.seat_number)"
                      />
                      <label
                        :for="`seat-${seat_.seat_col}-${seat_.seat_row}`"
                        :title="`${seat_.seat_number} | ${seat_.seat_type} | ${seat_.price.price_kes_amount}`"
                      >
                        <span class="seat-text">{{seat_.seat_number}}</span>
                        <span class="armrest-right arm"></span>
                        <span class="armrest-left arm"></span>
                        <span class="backrest"></span>
                      </label>
                    </div>
                  </div>
                </td>
              </tr>
            </table>
          </div>

          <div class="bus-seat-wrapper for-mobile p-b-2">
            <table border="0" cellpadding="2" cellspacing="1" class="bus-body w-100">
              <tr v-for="(row_, index) in activeTableRows" :key="index">
                <td v-for="(col_, index) in activeTableCols" :key="index">
                  <div v-for="(seat_, index) in seats" :key="index">
                    <div
                      :class="{'seat-item':true, 'business':seat_.seat_type === 'Business', 'vip':seat_.seat_type === 'VIP', 'first-class':seat_.seat_type === 'First Class'}"
                      v-if="row_ == seat_.seat_row && col_ == seat_.seat_col"
                    >
                      <input
                        type="checkbox"
                        :id="`seat-mobile-${seat_.seat_col}-${seat_.seat_row}`"
                        class="seat-checkbox"
                        @change="seatSelected(seat_, $event)"
                        v-bind:disabled="seat_.seat_number == 'Dr' || seat_.seat_status == 'reserved'"
                        :checked="isSeatSelected(seat_.seat_number)"
                      />
                      <label :for="`seat-mobile-${seat_.seat_col}-${seat_.seat_row}`">
                        <span class="seat-text">{{seat_.seat_number}}</span>
                        <span class="armrest-right arm"></span>
                        <span class="armrest-left arm"></span>
                        <span class="backrest"></span>
                      </label>
                    </div>
                  </div>
                </td>
              </tr>
            </table>
          </div>

          <div class="current-trip-details-panel w-33 mobile-w-100 mobile-mr-t-1">
            <div class="inline-form-wrapper">
              <form
                method="post"
                class="inline-form"
                @submit.prevent="busSeatSelectedEvent({boarding_point:trip.returning_boarding_point, dropping_point:trip.returning_dropping_point,selected_seats:return_selected_seats , is_return: true })"
                action="#"
              >
                <div class="form-group p-0 mr-b-1">
                  <label class id="boading-point">Boarding Point</label>
                  <v-select
                    v-model="trip.returning_boarding_point"
                    v-on:change="trip.returning_boarding_point = ''"
                    class="modern-selector is-inline"
                    placeholder="Select Boading Point"
                    label="branch_name"
                    id="boading-point"
                    :options="trip.return_selected_bus.pickup_branches"
                    required
                  ></v-select>
                </div>

                <div class="form-group p-0 mr-b-1">
                  <label class id="dropping-point">Dropping Point</label>
                  <v-select
                    v-model="trip.returning_dropping_point"
                    v-on:change="trip.returning_dropping_point = ''"
                    class="modern-selector is-inline"
                    placeholder="Select Dropping Point"
                    label="branch_name"
                    id="dropping-point"
                    :options="trip.return_selected_bus.dropoff_branches"
                    required
                  ></v-select>
                </div>

                <div class="form-group pricing-total-wrapper">
                  <div
                    class="row-item selected-seat-wrapper flexbox justify-between align-items-center p-b-1"
                  >
                    <span class="label font-semibold">Selected Seats</span>
                    <span class="value font-bold">
                      <span
                        v-for="(selected_seat, index) in return_selected_seats"
                        :key="index"
                      >{{ selected_seat.seat_number }},</span>
                    </span>
                  </div>
                  <div
                    class="row-item total-amount-wrapper flexbox justify-between align-items-center p-b-1"
                  >
                    <span class="label font-semibold">Total Amount</span>
                    <span class="value font-bold">KES {{ return_total_amount | formatCurrency }}</span>
                  </div>
                </div>

                <!-- Show form feedback here -->
                <div
                  class="form-group form-message text-center bg-white hidden"
                >Enter some form feedback here or error message</div>
                <!-- Show form feedback here -->

                <!-- Submit Form -->
                <div class="form-group">
                  <button class="btn" id="continue-btn">CONTINUE</button>
                </div>
                <!-- /Submit Form -->
              </form>
            </div>
          </div>
        </div>
      </div>
    </section>

    <!-- App Footer -->
    <AppFooter />

    <!-- App Footer -->
    <AppModal v-if="showModal" @close="showModal = false" @demoModalContinue="modalClosedEvent"></AppModal>
  </div>
</template>

<script>
/*Vuex Data & Actions*/
import { mapGetters, mapActions } from "vuex";

//AppHeader component
import AppHeader from "@/components/AppHeader.vue";

//AppTLoader component
import AppLoader from "@/components/AppLoader.vue";

//AppFooter component
import AppFooter from "@/components/AppFooter.vue";

//AppModal component
import AppModal from "@/components/AppModal.vue";

export default {
  name: "AppReturnBusSeats",
  components: {
    AppHeader,
    AppLoader,
    AppFooter,
    AppModal
  },
  data() {
    return {
      busSeatLoadingComplete: false,
      activeTableCols: 5,
      activeTableRows: 3,
      buseData: {},
      seats: null,
      max_seats: 0
    };
  },
  computed: mapGetters([
    "trip",
    "return_selected_seats",
    "selected_seats",
    "return_total_amount",
    "show_Loader",
    "showModal"
  ]),
  methods: {
    ...mapActions([
      "getBusSeats",
      "getSelectedSeats",
      "removeUnSelectedSeats",
      "getTotalAmount",
      "busSeatSelectedEvent",
      "modalClosedEvent",
      "hideModal",
      "showWarning",
      "showError",
      "getActiveReturningSeat"
    ]),
    isSeatSelected(seat_number) {
      return this.return_selected_seats.some(
        seat => seat["seat_number"] === seat_number
      );
    },
    seatSelected: function(seat_, event) {
      if (event.target.checked) {
        this.max_seats += 1;

        if (this.max_seats >= 2) {
          event.target.checked = false;
          //Alert
          this.showWarning("Sorry, maximum number of seats selected");

          return false;
        }

        if (this.selected_seats.length <= this.return_selected_seats.length) {
          event.target.checked = false;
          //Alert
          this.showWarning(
            "Your return seats are more that your onward seats."
          );

          return false;
        }

        this.getSelectedSeats({ seat: seat_, is_return: true });
        this.getTotalAmount({ is_return: true });

        //Set Active Returning Seat
        this.getActiveReturningSeat({ seat: seat_ });

        //Lock Seats
        //this.getLockSeats({ seat_is_return: true, seat: seat_ });
      } else {
        this.max_seats = 0;
        this.removeUnSelectedSeats({ seat: seat_, is_return: true });
        this.getTotalAmount({ is_return: true });
      }
    },
    getBusDetailsRequest: function() {
      let self = this;
      var requestOptions = {
        method: "GET",
        redirect: "follow"
      };

      fetch(
        `http://193.200.241.91:8000/api/v2/schedules/${self.trip.return_selected_bus.schedule_id}/seats?town_from_id=${self.trip.town_from_return.id}&town_to_id=${self.trip.town_to_return.id}`,
        requestOptions
      )
        .then(response => {
          return response.json();
        })
        .then(data => {
          //Set bus seats to store
          this.getBusSeats({ seats: data, is_return: true });

          let _busData = data.bus_type;
          self.seats = data.seats;
          self.buseData = data.bus_type;
          self.activeTableCols = _busData.bus_type_cols;
          self.activeTableRows = _busData.bus_type_rows;
          self.busSeatLoadingComplete = true;
        })
        .catch(error => {
          //Alert
          this.showError("Sorry, error occured!");

          console.log(`Server error ${error}`);
        });
    }
  },
  mounted() {
    this.getBusDetailsRequest();

    //scroll to top
    window.scrollTo(0, 0);
  }
};
</script>

